import {
  ChangeDetectionStrategy,
  Component,
  OnInit,
  HostBinding,
  Output,
  EventEmitter,
  OnChanges,
  Input
} from "@angular/core";
import { NgModule } from "@angular/core";
import { SearchService } from "../search.service";
import { DetailsService } from "../details.service";
import { FavoriteService } from "../favorite.service";
import { error } from "selenium-webdriver";

import {MatButtonModule, MatCheckboxModule} from '@angular/material';

import {MatTooltipModule} from '@angular/material/tooltip';
import {NgxPaginationModule} from 'ngx-pagination';


declare var google: any;

@Component({
  selector: "app-result-table",
  templateUrl: "./result-table.component.html",
  styleUrls: ["./result-table.component.css"],
  // changeDetection: ChangeDetectionStrategy.OnPush

})

@NgModule({

  imports: [MatButtonModule, MatCheckboxModule]

})
export class ResultTableComponent implements OnInit {
  @Output() slide = new EventEmitter<any>();

  page: number = 1;
  itemsPerPage : number = 10;

  showResult = false;
  resultJson = null;
  nextPage: any;
  prevPage: any = false;
  curPage = 1;
  service: any;
  @Input("place") selectedRow: any;
  geoJson: any;
  isFavorite: any;
  error: boolean = false;
  startLocation = "";
  title : any;

  // index : [] as number[];
  index: number[] = [];

  constructor(
    private sService: SearchService,
    private dService: DetailsService,
    private fService: FavoriteService
  ) {
    this.sService.resultJson.subscribe(data => {
      //console.log(data);
      if (data === null) {
        this.error = true;
        this.showResult = true;
      } else if (data === undefined) {
      }
      else if (data["status"] == 'ZERO_RESULTS') {
        this.resultJson = null;
        this.showResult = true;
      }
      else if(data == 'clear') {
        this.resultJson = null;
        this.showResult = true;
        this.selectedRow = null;
      }      
      else if(data["findItemsAdvancedResponse"]["0"]["searchResult"] == undefined || data["findItemsAdvancedResponse"]["0"]["searchResult"]["0"]["item"] == undefined) {
        this.resultJson = null;
        this.showResult = true;
      }
       else {
        // console.log(data);
        this.resultJson = data["findItemsAdvancedResponse"]["0"]["searchResult"]["0"]["item"];
        this.checkFavorite();
        // this.nextPage = data["findItemsAdvancedResponse"]["0"]["paginationOutput"]["0"]["pageNumber"];
        //this.geoJson = data["geoJson"];
        //this.startLocation = data["startLocation"];
        this.error = false;
        this.showResult = true;
        // console.log(this.resultJson); 
        for (let i = 0; i < this.resultJson.length; i++) {
            this.index[i] = i;    
        }
        // console.log(this.index);
      }
    });
    this.fService.isStorageChange.subscribe(data => {
      this.checkFavorite();
    });
    
  }

  getIndex(page, itemsPerPage, i) {
    // console.log(i);
    return itemsPerPage * (page - 1) + i + 1;
    
  }




  getTitle(title) {
    if (title["0"].length > 35) {
      let temCh = title["0"].charAt(35);
      if (temCh != " ") {
        for (let i = 34; i >= 0; i--) {
          if (title["0"].charAt(i) == " ") {
            return title["0"].substring(0, i) + "...";
          }
        }
      }
      return title["0"].substring(0, 35) + "...";
    }
    return title;
  }

  highlightRow(placeId) {
    // console.log(placeId);
    this.selectedRow = placeId;
  }

  showDetails() {
    this.slide.emit({ slide: "left", place: this.selectedRow });
  }

  getDetails(placeId, index) {
    //console.log(placeId);
    let temp = (this.page-1)*this.itemsPerPage+index;
    this.highlightRow(placeId);
    //console.log(index);
    this.dService.getDetails(placeId, temp);
    this.slide.emit({ slide: "left", place: placeId });
  }

  // getDetails() {
  //   console.log("get it");
  // }
  setFavorite(index) {
    let tempIndex = (this.page-1)*this.itemsPerPage+index;
    if (this.isFavorite[tempIndex]) {
      this.fService.removeFavorite(this.resultJson[tempIndex]["itemId"]);
      this.isFavorite[tempIndex] = false;
    } else {
      this.fService.saveFavorite(
        this.resultJson[tempIndex]["itemId"]["0"],
        this.resultJson[tempIndex]["galleryURL"],
        this.resultJson[tempIndex]["title"],
        this.resultJson[tempIndex]["sellingStatus"]["0"]["currentPrice"]["0"]["__value__"],
        this.resultJson[tempIndex]["shippingInfo"]["0"]["shippingServiceCost"]["0"]["__value__"],
        this.resultJson[tempIndex]["sellerInfo"]["0"]["sellerUserName"],
        this.resultJson[tempIndex]["viewItemURL"]["0"]
      );
      this.isFavorite[tempIndex] = true;
      // console.log(this.resultJson[tempIndex]);
    }
  }

  checkFavorite() {
    if (this.resultJson) {
      let place_id_arr = this.resultJson.map(data => data.itemId);
      // let place_id_arr = this.resultJson;
      this.isFavorite = this.fService.isFavorited(place_id_arr);
    }
  }

  ngOnInit() {
    this.sService.loadSearchResult();
    // this.curPage = 1;
    // console.log(this.resultJson);
  }
}
