import { Injectable, EventEmitter, Output } from "@angular/core";
import { HttpClient, HttpHeaders, HttpParams } from "@angular/common/http";
import { Subject } from "rxjs/Subject";
import { Observable } from "rxjs/Observable";

const httpOptions = {
  headers: new HttpHeaders({ "Content-Type": "application/json" })
};

// declare var google:any;

@Injectable()
export class SearchService {
  showResult = false;
  // response = new Observable<any>();
  private _resultJson = new Subject();
  resultJson = this._resultJson.asObservable();
  service: any;
  private jsonData: any;
  private _detailsJson = new Subject();
  detailsJson = this._detailsJson.asObservable();

  private _zip = new Subject();
  zip = this._zip.asObservable();

  private _isClear = new Subject();
  isClear = this._isClear.asObservable();

  private _isDataGet = new Subject();
  isDataget = this._isDataGet.asObservable();

  private _locationInput = new Subject();
  locationInput = this._isDataGet.asObservable();

  constructor(private http: HttpClient) {}

  private searchResults: any;
  private pageSaved: Array<string> = [];

  curPage = 1;
  

  search(form) {
    this.showResult = true;
    this.pageSaved.splice(0, this.pageSaved.length);
    let geo = form.geoJson;
    //console.log(form.keyword);
    let location = form.location;
    // console.log(form.keyword);
    // console.log(encodeURIComponent(form.keyword));
    let params = new HttpParams()
      .set("keyword", encodeURIComponent(form.keyword))
      .set("category", form.category)
      .set("distance", form.distance || '10')

      .set("condition_unspecified", form.condition_unspecified)
      .set("condition_new", form.condition_new)
      .set("condition_used", form.condition_used)

      .set("shipOption_pick", form.shipOption_pick)
      .set("shipOption_ship", form.shipOption_ship)

      .set("isUserInput", form.isUserInput || false)
      .set("location", location)
      .set("geoJson", JSON.stringify(form.geoJson));
    // console.log(form.isUserInput);

    let response = this.http.get("/api/search", { params: params });
    // let response = this.http.get("http://localhost:3000/api/search", { params: params });

    //console.log(response);
    response.subscribe(
      data => {
        this.jsonData = data;
        this.jsonData.geoJson = geo
        this.jsonData.startLocation = location;
        this._resultJson.next(this.jsonData);
        this._isDataGet.next(true);
        if (data["findItemsAdvancedResponse"]["0"]["searchResult"] != undefined && data["findItemsAdvancedResponse"]["0"]["searchResult"]["0"]["item"] != undefined)
        this.searchResults = data["findItemsAdvancedResponse"]["0"]["searchResult"]["0"]["item"];
        // console.log(this.jsonData)
      },
      err => {
        this._resultJson.next(null);
      }
    );
  }

  loadSearchResult() {
    this._resultJson.next(this.jsonData);
  }

  getZipCode(zip) {
    // console.log(zip)
    if (zip.length != 0) {
      let httpParams = new HttpParams().set("zip", zip);
      let response = this.http.get("/api/zip_info", { params: httpParams });
      // let response = this.http.get("http://localhost:3000/api/zip_info", { params: httpParams });

      // console.log(response);
      response.subscribe(
        data => {
          this._zip.next(data);
          // console.log(data);
        },
      );
    }
  }

  getGeolocation() {
    let url = "http://ip-api.com/json";
    //console.log(1);
    return this.http.get(url);
  }

  clear() {
    this._resultJson.next('clear');
    this.jsonData = undefined;
    this._isClear.next(true);
    this.pageSaved.splice(0, this.pageSaved.length);
  }
}
