import { Component, OnInit, Output, EventEmitter, ChangeDetectorRef, OnChanges, Input, NgZone } from "@angular/core";
import { SearchForm } from "./search-form";
import { NgForm } from "@angular/forms/src/directives/ng_form";
//import { FormsModule } from '@angular/forms';
import { SearchService } from "../search.service";
// import { HttpClient } from '@angular/common/http';
import {MatAutocompleteModule} from '@angular/material/autocomplete';
import { FormControl, Validators } from '@angular/forms';


@Component({
  selector: "app-search-form",
  templateUrl: "./search-form.component.html",
  styleUrls: ["./search-form.component.css"]
})
export class SearchFormComponent implements OnInit {
  // @Input() form: SearchForm;
  userLocation: Object;
  gotgeojson: boolean = false;
  category = "All Categories";
  form = SearchForm;
  zipJson : any;

  options = []; 
  temp : any;
  temp2 : any;
  flag = 1;
  searchDis : any;


  // checkVal = new FormControl('', [Validators.required, Validators.]);
  // checkVal = new FormControl('', Validators.pattern('[0-9]{5}'));
  


  constructor(private sService: SearchService, private cdRef: ChangeDetectorRef, private zone: NgZone) {
    
  }

  checkDisabled() {
    if(!this.form.isUserInput) {
      this.form.location = '';
      return false;
    }
      
    if (this.checkValid(this.form.location)) {
      return true;
    }
    if (this.zipJson == undefined || this.zipJson["postalCodes"] == undefined || this.zipJson["postalCodes"].length == 0) {
      return true;
    } else {
      return false;
    }  
  }

  getZip() {
    if (this.form.location != null) {
      if (this.flag == 1) {
        this.temp = this.form.location;
        this.flag = 2;
        this.sService.getZipCode(this.form.location);
        this.sService.zip.subscribe(data => {
          this.zone.run(() => {
            this.zipJson = data;
          });
        });
      } else {
        if (this.form.location != this.temp) {
          this.sService.getZipCode(this.form.location);
          this.temp = this.form.location;
          this.sService.zip.subscribe(data => {
            this.zone.run(() => {
                this.zipJson = data;
            });
          });
        }
      }
      if (this.zipJson != undefined && this.zipJson["postalCodes"] != undefined) {
        let tempZip = this.zipJson["postalCodes"];
        for (let i = 0; i < 5; i++) {
          if (tempZip[i + ""] != undefined) {
            this.options[i] = tempZip[i + ""]["postalCode"];
          }        
        } 
      } 
    }
   return this.options;
  }


  clear() {

    // this.form.keyword = ""
    
    // this.form.reset();
    // this.flag2 = 2;
    this.form.keyword = '';
    this.form.category = "All Categories";
    this.form.isUserInput = false;
    this.form.condition_new = '';
    this.form.condition_used = '';
    this.form.condition_unspecified = '';
    this.form.shipOption_pick = '';
    this.form.shipOption_ship = '';
    this.form.distance = '';
    this.form.location = '';
    // this.form.geoJson = undefined;
    this.getGeo();
    // console.log(this.form);

    this.sService.clear();

  }


  checkValid(zip) {
    // console.log(zip);
    let reg = /^\d{5}$/;
    if (!this.form.isUserInput) {
      return false;
    }   
    return !reg.test(zip);
  }
  getAddressOnChange(event, location) {
    this.form.location = (<HTMLInputElement>document.getElementById('loc-input')).value;
  }

  getGeo() {
    this.sService.getGeolocation().subscribe(data => {
      this.userLocation = data["zip"];
      //console.log(this.userLocation);
      this.form.geoJson = this.userLocation;
      this.gotgeojson = true;
    });
  }

  // onSubmit() {

  // }

  submitIt() {
      this.sService.search(this.form);
  }

  searchTypes = [
    "All Categories",
    "Art",
    "Baby",
    "Books",
    "Clothing, Shoes & Accessories",
    "Computers/Tablets & Networking",
    "Health & Beauty",
    "Music",
    "Video Games & Consoles"
  ];

  ngOnInit() {
    //this.form.category = 'All Categories';
    //this.clear();
    // this.checkVal = new FormControl({value:'', disabled:false});
    this.getGeo();
  
    // this.checkVal = new FormControl({value:'', disabled:true});
  }
}
