import { Component, OnInit, Input, OnChanges } from "@angular/core";
import { DetailsService } from "../../details.service";
import { fadeInAnimation } from "../../fade-in.animation";
import * as moment from "moment";

@Component({
  selector: "app-reviews-tab",
  templateUrl: "./reviews-tab.component.html",
  styleUrls: ["./reviews-tab.component.css"],
  animations: [fadeInAnimation]
})
export class ReviewsTabComponent implements OnChanges {
  @Input() ggReviews: any;
  //@Input() disable: any;

  yelpReviews: any;
  reviewTypes = ["Default", "Product Name", "Days Left", "Price", "Shipping Cost"];
  selectedReviewType: number = 0;
  error = false;

  ggReviewOrderPointer = [0, 1, 2, 3, 4];
  yelpReviewOrderPointer = [0, 1, 2];

  ProductNamePointer = [];
  DaysLeftPointer = [];
  PricePointer = [];
  ShippingCostPointer = [];
  defaultPointer = [];

  // disable : any;

  show : number = 5;

  orderTypes = [
    "Ascending",
    "Descending"
  ];

  checkWin() {
    if (window.innerWidth < 600) {
      return true;
    } else {
      return false;
    }
  }

  countDay(index) {
    let tempDay = this.ggReviews[index]["timeLeft"];
    return tempDay.indexOf("D") - tempDay.indexOf("P");
  }
  setAscending() {
    if (this.selectedReviewType == 1) {
      this.ProductNamePointer.sort((a, b) => {
        if (this.ggReviews[a]["title"] > this.ggReviews[b]["title"]) {
          return 1;
        } else {
          return -1;
        }
      });
    } else if (this.selectedReviewType == 2) {
      this.DaysLeftPointer.sort((a, b) => {
        if (this.countDay(a) > this.countDay(b)) {
          return 1;
        } else {
          return -1;
        }
      });
    } else if (this.selectedReviewType == 3) {
      this.PricePointer.sort((a, b) => {
        if (this.ggReviews[a]["buyItNowPrice"]["__value__"] > this.ggReviews[b]["buyItNowPrice"]["__value__"]) {
          return 1;
        } else {
          return -1;
        }
      });
    } else if (this.selectedReviewType == 4) {
      this.ShippingCostPointer.sort((a, b) => {
        if (this.ggReviews[a]["shippingCost"]["__value__"] > this.ggReviews[b]["shippingCost"]["__value__"]) {
          return 1;
        } else {
          return -1;
        }
      });
    }
  }

  setDescending() {
    if (this.selectedReviewType == 1) {
      this.ProductNamePointer.sort((a, b) => {
        if (this.ggReviews[a]["title"] < this.ggReviews[b]["title"]) {
          return 1;
        } else {
          return -1;
        }
      });
    } else if (this.selectedReviewType == 2) {
      this.DaysLeftPointer.sort((a, b) => {
        if (this.countDay(a) < this.countDay(b)) {
          return 1;
        } else {
          return -1;
        }
      });
    } else if (this.selectedReviewType == 3) {
      this.PricePointer.sort((a, b) => {
        if (this.ggReviews[a]["buyItNowPrice"]["__value__"] < this.ggReviews[b]["buyItNowPrice"]["__value__"]) {
          return 1;
        } else {
          return -1;
        }
      });
    } else if (this.selectedReviewType == 4) {
      this.ShippingCostPointer.sort((a, b) => {
        if (this.ggReviews[a]["shippingCost"]["__value__"] < this.ggReviews[b]["shippingCost"]["__value__"]) {
          return 1;
        } else {
          return -1;
        }
      });
    }
  }


  ngOnChanges() {
    // console.log(this.ggReviews);
    if (this.ggReviews != undefined) {
      for (let i = 0; i < this.ggReviews.length; i++) {
        this.ProductNamePointer[i] = i;
        this.DaysLeftPointer[i] = i;
        this.PricePointer[i] = i;
        this.ShippingCostPointer[i] = i;
        this.defaultPointer[i] = i;
      }
    }
 
    // console.log(this.selectedReviewType);


    //this.ggReviewOrderPointer = Array.from(Array(this.ggReviews.length).keys());
  }

  choose() {
    if (this.selectedReviewType == 0) {
      return this.defaultPointer;
    } else if (this.selectedReviewType == 1) {
      return this.ProductNamePointer;
    } else if (this.selectedReviewType == 2) {
      return this.DaysLeftPointer;
    } else if (this.selectedReviewType == 3) {
      return this.PricePointer;
    } else if (this.selectedReviewType == 4) {
      return this.ShippingCostPointer;
    }
  }

  selectedOrderType: number = 0;

  setReviewType(type) {
    this.selectedReviewType = type;
    if (this.selectedReviewType == 0) {

    } else {
      //this.disable = false;
    } 
    //console.log(this.disable);
    this.error = false;
    this.setOrderType(this.selectedOrderType);
  }

  setOrderType(type) {
    this.selectedOrderType = type;
    switch (type) {
      case 0: {
        this.setAscending();
        return;
      }
      case 1: {
        this.setDescending();
        return;
      }
    }
  }

  constructor(private dService: DetailsService) {}

  ngOnInit() {

    // console.log(this.ggReviews);

  }
}
