import {
  Component,
  OnInit,
  OnChanges,
  Output,
  Input,
  EventEmitter
} from "@angular/core";
import { FavoriteService } from "../favorite.service";
import { DetailsService } from "../details.service";
import { SearchService } from "../search.service";

@Component({
  selector: "app-favorite",
  templateUrl: "./favorite.component.html",
  styleUrls: ["./favorite.component.css"]
})
export class FavoriteComponent implements OnInit {
  @Output() slide = new EventEmitter<any>();
  nextPage: boolean = false;
  prevPage: boolean = false;
  favorites: any;
  @Input("place") selectedPlace: any;
  private curPage = 1;

  constructor(
    private fService: FavoriteService,
    private dService: DetailsService,
    private sService: SearchService
  ) {
    this.fService.favorite.subscribe(data => {
      this.favorites = data["allFav"];
      // console.log(data);
      // if (!data["flag"]) {
      //   this.nextPage = true;
      // } else {
      //   this.nextPage = false;
      // }
    });
    this.sService.isClear.subscribe(data => {
      // console.log(data);
    })
  }

  calPrice() {
    let sum = 0.0;
    for (let i = 0; i < this.favorites.length; i++) {
      sum += parseFloat(this.favorites[i]["price"]);
    }
    return sum;
  }


  getTitle(title) {
    if (title["0"].length > 35) {
      let temCh = title["0"].charAt(35);
      if (temCh != " ") {
        for (let i = 34; i >= 0; i--) {
          if (title["0"].charAt(i) == " ") {
            return title["0"].substring(0, i) + "...";
          }
        }
      }
      return title["0"].substring(0, 35) + "...";
    }
    return title;
  }

  highLightRow(placeId) {
    this.selectedPlace = placeId;
  }

  showDetails() {
    this.slide.emit({ slide: "left", place: this.selectedPlace });
  }

  getDetails(key, index) {
    if (this.selectedPlace != key) {
      this.dService.getDetails(key, index);
    }
    this.highLightRow(key);
    this.slide.emit({ slide: "left", place: key });
  }

  removeFavorite(key) {
    this.fService.removeFavorite(key);
  }

  // getNextPage() {
  //   this.fService.getAllFavorite(this.curPage + 1);
  //   this.curPage++;
  //   if (this.curPage > 1) {
  //     this.prevPage = true;
  //   }
  // }

  // getPrevPage() {
  //   this.fService.getAllFavorite(this.curPage - 1);
  //   this.curPage--;
  //   if (this.curPage == 1) {
  //     this.prevPage = false;
  //   }
  // }

  loadFavorite() {
    this.fService.getAllFavorite(this.curPage);
  }

  ngOnInit() {
    this.loadFavorite();
  }
}
